import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import('../views/About.vue')
  },
  {
    path: '/profile',
    name: 'Profile',
    component: () => import('../views/Profile.vue')
  },
  {
    path: '/works',
    name: 'Works',
    component: () => import('../views/Works.vue')
    // children: [{
    //   path: '/works/week3',
    //   name: 'Week3',
    //   component: () => import(/* webpackChunkName: "about" */ '../views/Week3.vue')
    // },
    // {
    //   path: '/works/week5-9',
    //   name: 'Week5-9',
    //   component: () => import(/* webpackChunkName: "about" */ '../views/Week5-9.vue')
    // },
    // {
    //   path: '/works/week10-11',
    //   name: 'Week10-11',
    //   component: () => import(/* webpackChunkName: "about" */ '../views/Week10-11.vue')
    // }]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
