      <ul>
        <li v-for="(route,index) in items" :key="route.route_name">

          <!--          路由入口,循环渲染多个router-link/a标签-->
          <router-link :to={name:route.route_name}>{{text[index]}}</router-link>
        </li>
        <div class="language" v-on:click=switchlanguage()>
         <img draggable="false"
            src="https://hyxxzgc1104-1257397766.cos.ap-guangzhou.myqcloud.com/MyBlog/imgs/%E4%B8%AD%E8%8B%B1%E6%96%87.svg ">
