<script>
function switchlanguage () {
  if (this.language === 'en') {
    this.text = this.text_cn
    this.language = 'cn'
    console.log('切换到中文')
  } else {
    this.text = this.text_en
    this.language = 'en'
    console.log('切换到英文')
  }
}

export default {
  data () {
    return {
      items: [{
        route_name: 'Home'

      },
      {
        route_name: 'Profile'

      },
      {
        route_name: 'Works'

      },
      {
        route_name: 'About'

      }
      ],
      language: 'cn',
      text_en: ['Home', 'Profile', 'Works', 'About'],
      text_cn: ['主页', '简介', '作品', '关于'],
      text: []
    }
  },
  methods: {
    switchlanguage
  },
  created () {
    this.text = this.text_cn
  },
  name: 'Header'
}
</script>

